import {
  Button,
  Empty,
  Image as AntImage,
  Modal,
  notification,
  Spin
} from 'antd';
import React from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { getImage } from '../../../api/images';
import { useStoreContext } from '../../../context/Store';
import { Image } from '../../../types';
import { HeartOutlined } from '@ant-design/icons';
import { addFavourite, getFavourite } from '../../../api/favourites';
import { BreedInfo } from './BreedInfo';

const ImageModal: React.FC = () => {
  const { getStoreImage, addStoreFavourite } = useStoreContext();
  const [image, setImage] = React.useState<Image | undefined>();
  const [imageLoading, setImageLoading] = React.useState<boolean>(false);
  const [favouriteLoading, setFavouriteLoading] =
    React.useState<boolean>(false);

  const emptyImage =
    'data:image/png;base64,';

  const navigate = useNavigate();
  const { id: imageId } = useParams<'id'>();

  React.useEffect(() => {
    if (imageId) {
      const imageFromStore = getStoreImage(imageId);
      if (imageFromStore) {
        setImage(imageFromStore);
      } else {
        setImageLoading(true);
        getImage(imageId)
          .then((res) => {
            setImage(res);
          })
          .catch((e) => {
            notification.error({
              message: e.message,
              description: e.response?.data.message || ''
            });
          })
          .finally(() => {
            setImageLoading(false);
          });
      }
    }
  }, []);

  const onClose = () => {
    navigate('/images');
  };

  const onAdd = () => {
    if (imageId) {
      setFavouriteLoading(true);
      addFavourite({ image_id: imageId })
        .then((res) => {
          notification.success({ message: 'Successfully added' });
          getFavourite(res.id)
            .then((res) => {
              addStoreFavourite(res);
            })
            .catch((e) => {
              notification.error({
                message: e.message,
                description: e.response?.data.message || ''
              });
            });
        })
        .catch((e) => {
          notification.error({
            message: e.message,
            description: e.response?.data.message || ''
          });
        })
        .finally(() => {
          onClose();
          setFavouriteLoading(false);
        });
    }
  };

  return (
    <Modal
      className='image-modal'
      visible
      onCancel={onClose}
      destroyOnClose
      width={960}
      title={`Image ID is '${imageId}'`}
      footer={[
        <Button key='close' onClick={onClose}>
          Close
        </Button>,
        <Button
          key='add'
          loading={favouriteLoading}
          onClick={onAdd}
          icon={<HeartOutlined />}
          type='primary'>
          Add to Favourite
        </Button>
      ]}>
      <div
        style={{
          display: 'flex',
          flexDirection: 'column',
          position: 'relative',
          alignItems: 'center'
        }}>
        {!imageLoading ? (
          <>
            <AntImage
              src={image ? image.url : emptyImage}
              width={150}
              height={150}
              fallback={emptyImage}
            />
            {!!image?.breeds?.length ? (
              image.breeds.map((breed) => (
                <BreedInfo key={breed.id} breed={breed} />
              ))
            ) : (
              <Empty description='No breed info' style={{ marginTop: 50 }} />
            )}
          </>
        ) : (
          <Spin size='large' />
        )}
      </div>
    </Modal>
  );
};

export { ImageModal };
